body {
  background-color: #f3f4f6;
  font-family: Inter, sans-serif;
}

.table-container {
  overflow-x: auto;
}

.project-table {
  table-layout: fixed;
  width: 100%;
}

th, td {
  vertical-align: middle;
  word-wrap: break-word;
  padding: 12px 16px;
}

th {
  vertical-align: top;
}

input, select, textarea {
  background-color: #fff;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  width: 100%;
  padding: 8px;
  transition: border-color .2s;
}

input:focus, select:focus, textarea:focus {
  border-color: #4f46e5;
  outline: none;
  box-shadow: 0 0 0 2px #4f46e533;
}

input[type="date"]:read-only, input[type="text"]:read-only {
  cursor: not-allowed;
  background-color: #f9fafb;
}

span[data-key="plannedEnd"], span[data-key="forwardEnd"], span[data-key="taskId"] {
  padding: 8px;
  display: block;
}

textarea {
  resize: vertical;
  min-height: 40px;
  overflow-y: hidden;
}

.status-not-started {
  color: #4b5563;
  background-color: #e5e7eb;
}

.status-completed {
  color: #065f46;
  background-color: #d1fae5;
}

.status-blocked {
  color: #92400e;
  background-color: #fef3c7;
}

.delay-on-time {
  color: #065f46;
  background-color: #d1fae5;
}

.delay-positive {
  color: #991b1b;
  background-color: #fee2e2;
}

.delay-negative {
  color: #065f46;
  background-color: #d1fae5;
}

[contenteditable]:focus {
  background-color: #eef2ff;
  border-radius: 4px;
  outline: 2px solid #4f46e5;
}

.action-btn {
  flex-shrink: 0;
}

#file-ops-container {
  background-color: #fff;
  border-radius: .5rem;
  padding: 1rem 1.5rem;
  box-shadow: 0 4px 6px -1px #0000001a, 0 2px 4px -2px #0000001a;
}

.modal-overlay {
  z-index: 50000;
  opacity: 0;
  visibility: hidden;
  background: #0009;
  justify-content: center;
  align-items: center;
  transition: opacity .3s, visibility .3s;
  display: flex;
  position: fixed;
  inset: 0;
}

.modal-overlay.visible {
  opacity: 1;
  visibility: visible;
}

.modal-content {
  text-align: center;
  background: #fff;
  border-radius: .5rem;
  width: 450px;
  max-width: 90%;
  padding: 2rem;
  transition: transform .3s;
  transform: scale(.95);
  box-shadow: 0 10px 25px -5px #0000001a, 0 8px 10px -6px #0000001a;
}

.modal-overlay.visible .modal-content {
  transform: scale(1);
}

.modal-content p {
  margin-bottom: 1.5rem;
}

.column-width-select, .gantt-control-select {
  appearance: none;
  background-color: #f9fafb;
  background-image: none;
}

.cycle-error {
  background-color: #fff1f2 !important;
}

.cycle-error td {
  border-top: 2px solid #ef4444;
  border-bottom: 2px solid #ef4444;
}

.team-section {
  position: relative;
}

.team-section-active {
  z-index: 50;
}

.table-container.overflow-visible {
  overflow: visible;
}

.dependency-dropdown {
  position: relative;
}

.dependency-dropdown-button {
  text-align: left;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.dependency-dropdown-panel {
  z-index: 100;
  background-color: #fff;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  width: 100%;
  max-height: 500px;
  margin-top: 2px;
  display: none;
  position: absolute;
  overflow-y: auto;
  box-shadow: 0 4px 6px #0000001a;
}

.dependency-dropdown-panel.visible {
  display: block;
}

.dependency-option {
  cursor: pointer;
  align-items: center;
  padding: 8px 12px;
  display: flex;
}

.dependency-option:hover {
  background-color: #f3f4f6;
}

.dependency-option input[type="checkbox"] {
  width: 1rem;
  height: 1rem;
  margin-right: 8px;
}

.tooltip {
  cursor: help;
  position: relative;
}

.tooltip .tooltip-text {
  visibility: hidden;
  color: #fff;
  text-align: center;
  z-index: 100;
  opacity: 0;
  background-color: #374151;
  border-radius: 6px;
  width: 280px;
  margin-left: -140px;
  padding: 8px;
  font-weight: 500;
  transition: opacity .3s;
  position: absolute;
  bottom: 150%;
  left: 50%;
}

.tooltip:hover .tooltip-text {
  visibility: visible;
  opacity: 1;
}

.toggle-collapse-btn svg {
  transition: transform .2s ease-in-out;
}

.team-content {
  transition: max-height .3s ease-in-out;
  overflow: hidden;
}

.team-content.overflow-visible {
  overflow: visible;
}

.kpi-card {
  background-color: #f9fafb;
  border: 1px solid #e5e7eb;
  border-radius: .5rem;
  flex-direction: column;
  justify-content: space-between;
  padding: 1.5rem;
  display: flex;
}

.kpi-title {
  color: #6b7280;
  font-size: .875rem;
  font-weight: 500;
}

.kpi-value {
  color: #1f2937;
  font-size: 2.25rem;
  font-weight: 700;
}

.kpi-chart-container {
  grid-column: span 1 / span 1;
}

@media (width >= 640px) {
  .kpi-chart-container {
    grid-column: span 2 / span 2;
  }
}

@media (width >= 1024px) {
  .kpi-chart-container {
    grid-area: span 2 / span 1 / span 2 / span 1;
    height: 220px;
  }
}

.gantt-container {
  background-color: #fff;
  border: 1px solid #e5e7eb;
  border-radius: .5rem;
  display: grid;
}

.gantt-tasks {
  font-size: .875rem;
}

.gantt-resizer {
  cursor: col-resize;
  z-index: 1;
  background-color: #e5e7eb;
  width: 5px;
  position: relative;
}

.gantt-resizer:hover {
  background-color: #3b82f6;
}

.gantt-task-name {
  box-sizing: border-box;
  white-space: nowrap;
  text-overflow: ellipsis;
  border-bottom: 1px solid #e5e7eb;
  align-items: center;
  height: 37px;
  padding: 8px 12px;
  display: flex;
  overflow: hidden;
}

.gantt-timeline {
  position: relative;
  overflow-x: auto;
}

.gantt-header-container {
  z-index: 10;
  background-color: #f9fafb;
  position: sticky;
  top: 0;
}

.gantt-header {
  border-bottom: 1px solid #e5e7eb;
  display: flex;
}

.gantt-month {
  text-align: center;
  color: #4b5563;
  border-right: 1px solid #e5e7eb;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  height: 25px;
  font-weight: 600;
  display: flex;
}

.gantt-days {
  border-bottom: 1px solid #e5e7eb;
  display: flex;
}

.gantt-day {
  text-align: center;
  color: #6b7280;
  border-right: 1px solid #e5e7eb;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  height: 21px;
  display: flex;
}

.gantt-day.weekend {
  background-color: #f3f4f6;
}

.gantt-rows {
  position: relative;
}

.gantt-row {
  box-sizing: border-box;
  border-bottom: 1px solid #e5e7eb;
  height: 37px;
}

.gantt-weekend-col {
  z-index: 0;
  background-color: #f9fafb;
  position: absolute;
  top: 0;
  bottom: 0;
}

.gantt-bar {
  cursor: pointer;
  color: #1f2937;
  z-index: 2;
  background-color: #93c5fd;
  border: 1px solid #60a5fa;
  border-radius: 4px;
  align-items: center;
  height: 24px;
  margin-top: 6px;
  padding: 0 8px;
  font-size: .75rem;
  transition: background-color .2s;
  display: flex;
  position: absolute;
  overflow: visible;
}

.gantt-bar-baseline {
  z-index: 1;
  pointer-events: none;
  background-color: #a1a1aa;
  border-radius: 2px;
  height: 8px;
  margin-top: 26px;
  position: absolute;
}

.gantt-bar > span {
  white-space: nowrap;
}

.gantt-bar:hover {
  background-color: #60a5fa;
}

.gantt-bar.status-completed {
  background-color: #86efac;
  border-color: #4ade80;
}

.gantt-bar.status-completed:hover {
  background-color: #4ade80;
}

.gantt-bar.status-blocked {
  background-color: #fcd34d;
  border-color: #f59e0b;
}

.gantt-bar.status-blocked:hover {
  background-color: #f59e0b;
}

.gantt-bar.dimmed {
  opacity: .25;
}

.gantt-bar.is-critical {
  outline: 2px solid #ef4444;
}

.gantt-today-marker {
  z-index: 15;
  background-color: #ef4444;
  width: 2px;
  position: absolute;
  top: 0;
  bottom: 0;
}
/*# sourceMappingURL=projectplanner.c4429a5e.css.map */
