body {
  font-family: "Inter", sans-serif;
  background-color: #f3f4f6;
}
.table-container {
  overflow-x: auto;
}
.project-table {
  table-layout: fixed;
  width: 100%;
}
th,
td {
  padding: 12px 16px;
  vertical-align: middle;
  word-wrap: break-word;
}
th {
  vertical-align: top;
}
input,
select,
textarea {
  padding: 8px;
  border-radius: 6px;
  border: 1px solid #d1d5db;
  background-color: white;
  transition: border-color 0.2s;
  width: 100%;
}
input:focus,
select:focus,
textarea:focus {
  outline: none;
  border-color: #4f46e5;
  box-shadow: 0 0 0 2px rgba(79, 70, 229, 0.2);
}
input[type="date"]:read-only,
input[type="text"]:read-only {
  background-color: #f9fafb;
  cursor: not-allowed;
}
span[data-key="plannedEnd"],
span[data-key="forwardEnd"],
span[data-key="taskId"] {
  padding: 8px;
  display: block;
}
textarea {
  min-height: 40px;
  resize: vertical;
  overflow-y: hidden;
}
.status-not-started {
  background-color: #e5e7eb;
  color: #4b5563;
}
.status-completed {
  background-color: #d1fae5;
  color: #065f46;
}
.status-blocked {
  background-color: #fef3c7;
  color: #92400e;
}

.delay-on-time {
  background-color: #d1fae5;
  color: #065f46;
}
.delay-positive {
  background-color: #fee2e2;
  color: #991b1b;
}
.delay-negative {
  background-color: #d1fae5;
  color: #065f46;
}

[contenteditable]:focus {
  outline: 2px solid #4f46e5;
  background-color: #eef2ff;
  border-radius: 4px;
}
.action-btn {
  flex-shrink: 0;
}
#file-ops-container {
  background-color: white;
  padding: 1rem 1.5rem;
  border-radius: 0.5rem;
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
}
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 50000;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}
.modal-overlay.visible {
  opacity: 1;
  visibility: visible;
}
.modal-content {
  background: white;
  padding: 2rem;
  border-radius: 0.5rem;
  box-shadow: 0 10px 25px -5px rgb(0 0 0 / 0.1),
    0 8px 10px -6px rgb(0 0 0 / 0.1);
  text-align: center;
  max-width: 90%;
  width: 450px;
  transform: scale(0.95);
  transition: transform 0.3s;
}
.modal-overlay.visible .modal-content {
  transform: scale(1);
}
.modal-content p {
  margin-bottom: 1.5rem;
}
.column-width-select,
.gantt-control-select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-image: none;
  background-color: #f9fafb;
}
.cycle-error {
  background-color: #fff1f2 !important;
}
.cycle-error td {
  border-top: 2px solid #ef4444;
  border-bottom: 2px solid #ef4444;
}

.team-section {
  position: relative;
}
.team-section-active {
  z-index: 50;
}
.table-container.overflow-visible {
  overflow: visible;
}
.dependency-dropdown {
  position: relative;
}
.dependency-dropdown-button {
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.dependency-dropdown-panel {
  position: absolute;
  background-color: white;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  z-index: 100;
  max-height: 500px;
  overflow-y: auto;
  display: none;
  width: 100%;
  margin-top: 2px;
}
.dependency-dropdown-panel.visible {
  display: block;
}
.dependency-option {
  display: flex;
  align-items: center;
  padding: 8px 12px;
  cursor: pointer;
}
.dependency-option:hover {
  background-color: #f3f4f6;
}
.dependency-option input[type="checkbox"] {
  margin-right: 8px;
  height: 1rem;
  width: 1rem;
}

.tooltip {
  position: relative;
  cursor: help;
}
.tooltip .tooltip-text {
  visibility: hidden;
  width: 280px;
  background-color: #374151;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 8px;
  position: absolute;
  z-index: 100;
  bottom: 150%;
  left: 50%;
  margin-left: -140px;
  opacity: 0;
  transition: opacity 0.3s;
  font-weight: 500;
}
.tooltip:hover .tooltip-text {
  visibility: visible;
  opacity: 1;
}

/* --- Team Collapse Styles --- */
.toggle-collapse-btn svg {
  transition: transform 0.2s ease-in-out;
}
.team-content {
  transition: max-height 0.3s ease-in-out;
  overflow: hidden;
}
/* --- FIX: New class to allow dropdown to overflow --- */
.team-content.overflow-visible {
  overflow: visible;
}

/* --- Dashboard Styles --- */
.kpi-card {
  background-color: #f9fafb;
  border: 1px solid #e5e7eb;
  border-radius: 0.5rem;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.kpi-title {
  font-size: 0.875rem;
  font-weight: 500;
  color: #6b7280;
}
.kpi-value {
  font-size: 2.25rem;
  font-weight: 700;
  color: #1f2937;
}
.kpi-chart-container {
  grid-column: span 1 / span 1;
}
@media (min-width: 640px) {
  .kpi-chart-container {
    grid-column: span 2 / span 2;
  }
}
@media (min-width: 1024px) {
  .kpi-chart-container {
    grid-column: span 1 / span 1;
    grid-row: span 2 / span 2;
    height: 220px;
  }
}

/* --- Gantt Chart Styles --- */
.gantt-container {
  display: grid;
  border: 1px solid #e5e7eb;
  border-radius: 0.5rem;
  background-color: #fff;
}
.gantt-tasks {
  font-size: 0.875rem;
}
.gantt-resizer {
  position: relative;
  cursor: col-resize;
  width: 5px;
  background-color: #e5e7eb;
  z-index: 1;
}
.gantt-resizer:hover {
  background-color: #3b82f6;
}
.gantt-task-name {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 8px 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  border-bottom: 1px solid #e5e7eb;
  height: 37px; /* Unified row height */
}
.gantt-timeline {
  position: relative;
  overflow-x: auto;
}
.gantt-header-container {
  position: sticky;
  top: 0;
  background-color: #f9fafb;
  z-index: 10;
}
.gantt-header {
  display: flex;
  border-bottom: 1px solid #e5e7eb;
}
.gantt-month {
  flex-shrink: 0;
  text-align: center;
  font-weight: 600;
  border-right: 1px solid #e5e7eb;
  color: #4b5563;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 25px;
}
.gantt-days {
  display: flex;
  border-bottom: 1px solid #e5e7eb;
}
.gantt-day {
  flex-shrink: 0;
  text-align: center;
  color: #6b7280;
  border-right: 1px solid #e5e7eb;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 21px;
}
.gantt-day.weekend {
  background-color: #f3f4f6;
}
.gantt-rows {
  position: relative;
}
.gantt-row {
  height: 37px; /* Unified row height */
  border-bottom: 1px solid #e5e7eb;
  box-sizing: border-box;
}
.gantt-weekend-col {
  position: absolute;
  top: 0;
  bottom: 0;
  background-color: #f9fafb;
  z-index: 0;
}
.gantt-bar {
  position: absolute;
  height: 24px;
  margin-top: 6px;
  border-radius: 4px;
  background-color: #93c5fd;
  border: 1px solid #60a5fa;
  cursor: pointer;
  transition: background-color 0.2s;
  color: #1f2937;
  font-size: 0.75rem;
  z-index: 2;
  display: flex;
  align-items: center;
  padding: 0 8px;
  overflow: visible;
}
.gantt-bar-baseline {
  position: absolute;
  height: 8px;
  margin-top: 26px; /* Position below main bar */
  border-radius: 2px;
  background-color: #a1a1aa; /* Gray color for baseline */
  z-index: 1; /* Below main bar */
  pointer-events: none; /* Not interactive */
}
.gantt-bar > span {
  white-space: nowrap;
}
.gantt-bar:hover {
  background-color: #60a5fa;
}
.gantt-bar.status-completed {
  background-color: #86efac;
  border-color: #4ade80;
}
.gantt-bar.status-completed:hover {
  background-color: #4ade80;
}
.gantt-bar.status-blocked {
  background-color: #fcd34d;
  border-color: #f59e0b;
}
.gantt-bar.status-blocked:hover {
  background-color: #f59e0b;
}
.gantt-bar.dimmed {
  opacity: 0.25;
}
.gantt-bar.is-critical {
  outline: 2px solid #ef4444;
}
.gantt-today-marker {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 2px;
  background-color: #ef4444;
  z-index: 15;
}
